import React, { Component } from 'react';


export default class Story extends Component {
  constructor(props) {
    super(props);
    this.state = {
      text: '',
      url: '',
      a: ''
    };
  }
  componentDidMount() {
    this.setState(
      {
        text: this.props.text,
        url: this.props.url,
        a: this.props.a
      },
      () => {} //console.log(`text:${this.state.text}  url:${this.state.url}`)
    );
  }
  render() {
    return (
      <div>
        <p>{this.state.text}</p>
        <a target='_blank' rel='noopener noreferrer'  href={this.state.url}><span className='a_text' >{this.state.a}</span></a>
      </div>
    );
  }
}



