import React, { Component } from 'react';
// import storiesData from './StoriesData';
import Story from './components/Story';
import storiesData from './storiesData.json'
import './index.css';
import kamalaImage from './resources/Kamala_D_Harris.jpg'
// import Email from './components/Email'
import Popup from 'react-popup';

export default class Home extends Component {
  constructor(props) {
    super(props);
    this.state = {
      stories: [],
      email: '',
      img: ''
    };
  }
  componentDidMount() {
    let stories = []
    storiesData.forEach(story => {
      stories.push(story);
    });
    this.setState({
      stories: stories,
      img: kamalaImage,
    })
  }
  handleEmail = event => {
    this.setState({ email: event.target.value });
  };  
  render() {
    //if (this.state.stories.length === 0) {
    //  return <div />;
    //}
    //console.log('render stories:', this.state.stories.length)
    const stories = this.state.stories.map(story => {
      return (
        <li key={story.url} className='none'>
          <Story text={story.text} url={story.url} a={story.a}/>
        </li>
      );
    });

    //const stories = <li key='1'> <Story text={this.state.stories[0].text} url={this.state.stories[0].url} /></li>
    //  console.log('stories:',stories)
    return (
      <div>
        <h2 className='warning'>Clicked Links Open in a New Tab</h2>
        <img src={this.state.img} alt='' width='237' height='365' />
        <ul>{stories}</ul>
        <form onSubmit={() => {
          console.log('email:',this.state.email)
          Popup.create({
            title: 'Email',
            content: this.state.email,          
            className: 'alert',
            buttons: {
                right: ['close me and delete email']
            }
        });          
          //this.toggleEmail()
          //this.killUpdate = true
        }}>
          <input onChange={this.handleEmail} type='text' placeholder='   enter email hold k' size='32' />
        </form>   
      </div>
    );
  }
}
